import {sentences} from 'sbd';
import {compareTwoStrings} from 'string-similarity';

import { diffWords } from 'diff';

/* global document, Office, Word */
Office.onReady(info => {
  if (info.host === Office.HostType.Word) {
    document.getElementById("sideload-msg").style.display = "none";
    document.getElementById("app-body").style.display = "flex";
    document.getElementById("run").onclick = insertTable;
    document.getElementById("check").onclick = checkTable;
    document.getElementById("setupAll").onclick = setupAll;

  }
});




async function setupAll() {
  await Word.run(async (context) => {

    let allText = context.document.body
    allText.load("text");
    await context.sync();    

    context.document.body.clear();

    await context.sync();    

    let table = context.document.body.insertTable(2,1,"Start");
    table.getCell(0, 0).body.insertText(allText.text,"Start");
    table.getCell(0, 0).body.getRange().select(Word.SelectionMode.select);
    await context.sync();    
    
  });
}



function shuffleFisherYates(array) {
  let i = array.length;

  while (i--) {
    const ri = Math.floor(Math.random() * (i + 1));
    [array[i], array[ri]] = [array[ri], array[i]];
  }
  return array.join(" | ");
}

 
async function checkTable() {
  await Word.run(async (context) => {
       
    let rowsCount = context.document.body.tables.getFirst();
    rowsCount.load('rowCount');
    await context.sync();    


    let index = context.document.getSelection().parentTableCell;
    index.load("rowIndex");
    await context.sync();    

    let index2 = index.rowIndex;

      let firstCell = context.document.body.tables.getFirst().getCell(index2, 0).body;
      firstCell.load("text");
      let secondCell = context.document.body.tables.getFirst().getCell(index2, 3).body;
      secondCell.load("text");
      await context.sync();    



      let ssm = compareTwoStrings(firstCell.text, secondCell.text);

      context.document.body.tables.getFirst().getCell(index2, 4).body.clear();
     let df = diffWords(firstCell.text, secondCell.text, {ignoreCase:true, ignoreWhitespace:true});

     context.document.body.tables.getFirst().getCell(index2, 5).body.clear();
     context.document.body.tables.getFirst().getCell(index2, 5).body.insertText( Math.round(ssm * 10).toString(), "End")

     if (ssm > 0.85) context.document.body.tables.getFirst().getCell(index2, 0).body.font.set({
      color: "blue",
      size: 10
    });
    

           await context.sync();    


     df.forEach(element => {
      
       if (element.removed) {
        let thirdSentence =  context.document.body.tables.getFirst().getCell(index2, 4).body.insertText( "???????????????".substr(0,  element.value.length) + ' ', "End");
        thirdSentence.font.set({
          bold: false,
          italic: false,
          color: "blue",
          size: 12 });
  
        } else if (element.added) {
          let thirdSentence =  context.document.body.tables.getFirst().getCell(index2, 4).body.insertText( element.value, "End");
          thirdSentence.font.set({
            bold: false,
            italic: false,
            color: "red",
            size: 12 }); 
        } else {
          let thirdSentence =  context.document.body.tables.getFirst().getCell(index2, 4).body.insertText( element.value, "End");
          thirdSentence.font.set({
            bold: false,
            italic: false,
            color: "green",
            size: 12 });
        } 
     }); 
  

     await context.sync();








  });
}



// eslint-disable-next-line no-unused-vars
async function insertTable() {
  await Word.run(async (context) => {

      let firstCell = context.document.body.tables.getFirst().getCell(0, 0).body
      firstCell.load("text");

      let secondCell = context.document.body.tables.getFirst().getCell(1, 0).body
      secondCell.load("text");

      await context.sync();

     // let sentences = firstCell.text.split(".");
      //let sentences2 = secondCell.text.split(".");

      let sents = sentences( firstCell.text);
      let sentences2 = sentences(secondCell.text);

      context.document.body.clear();
      await context.sync(); 

       let table = context.document.body.insertTable(1,6,"Start", [['Source','Translate','Random','Your','Check', 'Mark']]);
  
       for (let i = 0; i < sents.length; i++) {

        let newText =  shuffleFisherYates(sents[i].split(" "));

   

        table.addRows('End', 1, [[sents[i],sentences2[i], newText]]);
        table.getCell(i+1, 0).body.font.set({
          color: "white",
          size: 10
        });
   
        

      }

       await context.sync(); 


    

  });
}




export async function run() {
  return Word.run(async context => {
    /**
     * Insert your Word code here
     */

    // insert a paragraph at the end of the document.
    const paragraph = context.document.body.insertParagraph("Hello World", Word.InsertLocation.end);

    // change the paragraph color to blue.
    paragraph.font.color = "blue";


  });
}
